.products-d {
    &__header {
        position: fixed;
        z-index: 15;
    }

    &__wrapper {
        margin-top: 44px;
        padding-bottom: 75px;
    }

    &__wrapper-info {
        .btn {
            margin-top: 20px;
        }
    }

    &__wrapper-img-wrap {
        position: relative;
        width: 100%;
        max-width: 350px;

        .slick-slide {
            position: relative;
            display: flex;
        }

        .slick-slide::after {
            position: absolute;
            content: '';

            top: 15px;
            left: 15px;

            width: 29px;
            height: 27px;
            background: url('../../icons/hearth.svg');
            background-size: cover;
            cursor: pointer;
        }

        .dots-horizontal {
            position: absolute;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 62px;
            bottom: 24px;
            left: 23px;
            z-index: 10;
        }
        
        .dots-horizontal li button {
            width: 100%;
            height: 100%;
            position: relative;
            font-size: 0;
        }

        .dots-horizontal li button:before {
            content: url('../../icons/close-white.svg');
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-size: cover;
            z-index: -1; 
        }

        .dots-horizontal li.slick-active button:before {
            content: url('../../icons/close.svg');
        }
    }

    &__wrapper-descr {
        margin-top: 20px;
        display: flex;
        flex-direction: column;

        h3 {
            font-size: 18px;
            line-height: 21px;
            text-transform: uppercase;
            margin-bottom: 5px;
        }

        span {
            font-size: 14px;
            line-height: 16px;
            color: var(--primary-500);
            margin-bottom: 5px;
        }

        .price {
            font-size: 24px;
            font-weight: 900;
            line-height: 19px;
            color: var(--black);
        }
    }

    &__wrapper-size {
        margin-top: 15px;

        a {
            position: relative;
            display: flex;
            margin-top: 10px;
            width: max-content;
            font-size: 16px;
            line-height: 18px;
            color: var(--black);
        }

        a::after {
            position: absolute;

            content: '';

            bottom: -1px;
            left: 0;

            width: 100%;
            height: 1px;

            background-color: var(--black);
        }
    }

    &__wrapper-about {
        margin-top: 20px;

        h3 {
            font-size: 24px;
            line-height: 28px;
        }

        p {
            margin-top: 10px;
            font-size: 16px;
            line-height: 18px;
        }
    }
}