.placing {
    display: flex;
    flex-direction: column;

    gap: 25px;

    justify-content: space-between;

    &-wrapper {
        margin-top: 20px;
    }

    &-wrapper__address,
    &-wrapper__delivery {
        span {
            font-size: 12px;
            font-weight: 400;
            line-height: 14px;
            color: rgba(0, 0, 0, 0.5);
        }

        p {
            font-size: 18px;
            font-weight: 400;
            line-height: 21px;
        }
    }

    &-wrapper__delivery {
        margin-top: 20px;

        &-item {
            color: var(--green);
        }
    }

    &-wrapper__products {
        margin-top: 20px;

        p {
            font-size: 18px;
            font-weight: 400;
            line-height: 21px;
        }
    }

    &-wrapper__list {
        margin-top: 13px;
        display: flex;
        align-items: center;
        gap: 8px;

        li {
            display: flex;
            align-items: center;
            width: 65px;
            height: 78px;
        }

        li img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &-wrapper__payment {
        margin-top: 20px;
        display: flex;
        flex-direction: column;

        &-wrap {
            display: flex;
            flex-direction: column;
            gap: 10px;

            padding: 10px;

            border-top: 1px solid var(--black);
            border-bottom: 1px solid var(--black);
        }

        &-wrap span {
            font-size: 12px;
            font-weight: 400;
            line-height: 14px;
            color: rgba(0, 0, 0, 0.5);
        }

        &-number {
            font-size: 18px;
            font-weight: 400;
            line-height: 21px;
        }

        a {
            position: relative;
            margin-top: 10px;
            align-self: flex-end;
            width: max-content;
            font-size: 18px;
            font-weight: 400;
            line-height: 21px;
            color: var(--black);
        }

        a:after {
            position: absolute;

            content: '';

            width: 100%;
            height: 1px;

            bottom: 1px;
            left: 0;

            background-color: var(--black);
        }
    }

    &-total__info {
        display: flex;
        align-items: center;
        justify-content: space-between;

        width: 100%;

        margin-bottom: 25px;

        span {
            font-size: 24px;
            font-weight: 900;
            line-height: 33px;
        }
    }
}