.add-card {
    &__wrapper {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        gap: 20px;

        .btn {
            margin-top: 10px;
        }
    }

    &__modal {
        position: absolute;

        display: none;
        align-items: center;
        justify-content: center;

        width: 100%;
        height: 100%;

        top: 0;
        left: 0;

        background-color: rgba(0, 0, 0, 0.5);
        z-index: 21;

        &-box {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;

            gap: 28px;

            width: 350px;

            padding: 28px 17px 30px 17px;

            background-color: var(--white);
        }

        &-box__info {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            gap: 18px;

            img {
                display: flex;
                flex-shrink: 0;
                width: 28px;
                height: 21px;
            }

            span {
                text-align: center;
                font-size: 20px;
                font-weight: 400;
                line-height: 23px;
            }
        }

        &-box__btn {
            width: 100%;
        }
    }
}