html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: ar;
  src: url("../fonts/Arial-Regular.ttf"), url("../fonts/Arial-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: ar;
  src: url("../fonts/Arial-Bold.ttf"), url("../fonts/Arial-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4;
  font-family: "ar", sans-serif;
  letter-spacing: 2px;
  color: var(--black-100);
  background-color: var(--white);
}

.container {
  max-width: 1443px;
  margin-left: auto;
  margin-right: auto;
  padding: unset;
}

.section {
  padding-left: 20px;
  padding-right: 20px;
}

.section--filled {
  background-color: var(--white);
}

.section h1,
.section h2,
.section h3,
.section h4,
.section h5 {
  font-weight: 500;
}

.section h1 {
  font-size: 22px;
}

.section h2 {
  font-size: 20px;
}

.section h3 {
  font-size: 14px;
  color: var(--gray-700);
}

.section a {
  text-decoration: none;
}

.section .padd-t {
  padding: 20px 0;
}

.section .padd-b {
  padding-bottom: 100px;
}

@media only screen and (min-width: 1024px) {
  .section {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media only screen and (min-width: 1366px) {
  .section {
    padding-left: 72px;
    padding-right: 72px;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;
  letter-spacing: 2px;
  text-transform: uppercase;
  border: none;
}

.btn--primary {
  background-color: var(--primary-500);
  color: var(--white);
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
}

.btn--black {
  background-color: var(--black);
  color: var(--white);
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

.btn.added {
  color: var(--black);
  background-color: var(--white);
  border: 1px solid black;
}

.breadcrumbs-back {
  display: flex;
  align-items: center;
  gap: 23px;
}

.breadcrumbs-back a {
  display: flex;
  width: 18.41px;
  height: 17.41px;
}

.breadcrumbs-back span {
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.form-group label {
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.form-group input {
  display: flex;
  align-items: flex-start;
  padding: 13px 19px;
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
  border: 1px solid var(--black);
  background-color: var(--white);
  border-radius: unset;
}

.form-group input::placeholder {
  letter-spacing: 2px;
}

.form-row {
  display: flex;
  align-items: center;
  gap: 20px;
  width: 100%;
}

.nav {
  position: fixed;
  width: 100%;
  height: 80px;
  bottom: 0;
  left: 0;
  z-index: 20;
}

.nav.filled {
  background-color: #F5F4E4;
}

.nav.filled .nav-list__item a {
  color: var(--black);
}

.nav.filled .nav-list__item a .icon-home {
  fill: var(--black);
}

.nav-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 17px 20px 17px 30px;
  width: 100%;
}

@media only screen and (min-width: 365px) {
  .nav-list {
    padding: 17px 20px 17px 47px;
  }
}

.nav-list__item a {
  display: flex;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 2px;
  text-decoration: none;
  color: var(--white);
}

.nav-list__item a.active {
  font-weight: 700;
}

.nav-list__item a .icon-home {
  width: 33.16px;
  height: 42.35px;
  fill: var(--white);
  stroke: unset;
}

.content-section {
  display: none;
  grid-template-areas: "list-1 list-2" "list-1 list-2" "list-1 list-3" "list-1 list-3";
  place-content: flex-start;
  gap: 26px;
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 20px;
  background-color: var(--white);
  z-index: 15;
}

.content-section.active {
  display: grid;
}

.content-section__wrap {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.content-section__wrap.list-1 {
  grid-area: list-1;
  height: 80px;
}

.content-section__wrap.list-2 {
  grid-area: list-2;
  height: 80px;
}

.content-section__wrap.list-3 {
  grid-area: list-3;
  height: 80px;
}

.content-section__wrap.list-3 .content-section__item a {
  text-transform: uppercase;
}

.content-section__title {
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
  color: var(--black);
  text-transform: uppercase;
  text-decoration: none;
}

.content-section__list {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.content-section__item a {
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
  color: var(--black);
  text-decoration: none;
}

.content-section--about.active {
  display: flex;
  flex-direction: column;
  gap: 7px;
}

.content-section--about .content-section__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0;
}

.content-section--about .content-section__logo-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 22px;
}

.content-section--about .content-section__logo-wrap .icon-home {
  width: 37px;
  height: 48px;
}

.content-section--about .content-section__logo-wrap .icon-logo {
  width: 146px;
  height: 14px;
}

.content-section--about .content-section__dropdown-list {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  width: 100%;
}

.content-section--about .content-section__dropdown-item div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  cursor: pointer;
}

.content-section--about .content-section__dropdown-item div span {
  font-size: 20px;
  font-weight: 400;
  line-height: 23px;
}

.content-section--about .content-section__dropdown-item div .arrow {
  width: 14px;
  height: 14px;
  border: solid var(--black);
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 4.5px;
  transform: rotate(45deg);
  transition: .3s;
}

.content-section--about .content-section__dropdown-item div .arrow.active {
  transform: rotate(224deg);
}

.content-section--about .content-section__dropdown-body {
  margin-top: 14px;
  display: none;
  flex-direction: column;
  gap: 14px;
  margin-left: 20px;
  list-style-type: disc;
  transition: .3s;
}

.content-section--about .content-section__dropdown-body.active {
  display: flex;
}

.content-section--about .content-section__dropdown-body li a {
  font-size: 20px;
  font-weight: 400;
  line-height: 23px;
  color: var(--black);
  text-decoration: none;
}

.content-section--about .content-section__social-list {
  margin-top: 15px;
  display: flex;
  align-items: center;
  align-self: flex-start;
}

@media only screen and (min-width: 365px) {
  .content-section--about .content-section__social-list {
    gap: 14px;
  }
}

.content-section--about .content-section__newsletter {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-bottom: 100px;
  max-width: 405px;
}

.content-section--about .content-section__newsletter p {
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
}

.content-section--about .content-section__newsletter-list {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.content-section--about .content-section__newsletter-list li {
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

.content-section--about .content-section__newsletter-list li a {
  position: relative;
  font-weight: 700;
  color: var(--black);
  text-decoration: none;
}

.content-section--about .content-section__newsletter-list li a::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 1px;
  bottom: -1px;
  left: 0;
  background-color: var(--black);
}

.content-section--profile.active {
  display: flex;
  align-items: flex-start;
}

.content-section--profile .content-section__profile-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8px;
}

.content-section--profile .content-section__profile-item {
  align-self: stretch;
  height: 100%;
  min-height: 110px;
}

.content-section--profile .content-section__profile-item a {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 11px;
  gap: 12px;
  height: 100%;
  box-sizing: border-box;
  border: 1px solid var(--black);
  text-decoration: none;
}

.content-section--profile .content-section__profile-item a span {
  text-align: center;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  color: var(--black);
}

.header {
  position: fixed;
  top: 20px;
  left: 0;
  width: 100%;
  z-index: 10;
}

.header-wrap {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 15px;
  padding: 0 0 0 5px;
}

.header-wrap__field {
  display: none;
  width: 100%;
  height: 35px;
}

.header-wrap__field input {
  padding-left: 15px;
  width: 100%;
  height: 100%;
  background-color: var(--white);
}

.header-wrap__field input[type="text"] {
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 1px;
}

.header-wrap__field.active {
  display: flex;
}

.header-wrap__search {
  display: flex;
  cursor: pointer;
}

.hero-carousel, .hero-carousel__vertical {
  position: relative;
  width: 100%;
}

.hero-carousel li, .hero-carousel__vertical li {
  position: relative;
}

.hero-carousel li .shadow, .hero-carousel__vertical li .shadow {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 107%;
  background: linear-gradient(0deg, black 1.327%, rgba(0, 0, 0, 0) 44.243%);
  z-index: 1;
}

.hero-carousel .slick-list, .hero-carousel__vertical .slick-list {
  width: 100%;
  height: 100vh;
}

.hero-carousel .slick-slide div, .hero-carousel__vertical .slick-slide div {
  width: inherit;
}

.hero-carousel .slick-slide, .hero-carousel__vertical .slick-slide {
  border: none;
}

.hero-carousel .slick-slide div li > img, .hero-carousel__vertical .slick-slide div li > img {
  display: flex;
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.hero-carousel .dots-horizontal, .hero-carousel__vertical .dots-horizontal {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 104px;
  height: 8px;
  bottom: 23%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 14;
}

.hero-carousel .dots-horizontal li::after, .hero-carousel__vertical .dots-horizontal li::after {
  background: none;
}

.hero-carousel .dots-horizontal li button, .hero-carousel__vertical .dots-horizontal li button {
  width: 26px;
  height: 8px;
  background-color: #808080;
  font-size: 0;
}

