  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: visible;
  min-height: 565px;
  padding: 15px;
  box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.4);
  background-color: var(--white);
  background: linear-gradient(0deg, rgba(255, 255, 255, 0) 62.201%, rgba(255, 255, 255, 0.1) 98.026%), linear-gradient(0deg, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.3) 100%), white;
}

.gallery-wrapper__item:nth-child(1) {
  transform: rotateY(0deg) translateZ(170px);
}

.gallery-wrapper__item:nth-child(2) {
  transform: rotateY(90deg) translateZ(170px);
}

.gallery-wrapper__item:nth-child(3) {
  transform: rotateY(180deg) translateZ(170px);
}

.gallery-wrapper__item:nth-child(4) {
  transform: rotateY(270deg) translateZ(170px);
}

.gallery button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.7);
  border: none;
  padding: 10px;
  cursor: pointer;
  z-index: 1;
}

.gallery button.prev {
  left: 10px;
}

.gallery button.next {
  right: 10px;
}

.gallery-wrapper__images {
  width: 100%;
}

.gallery-wrapper__images img {
  width: 100%;
}

.gallery-wrapper__info {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
}
