.gallery {
    width: 100%;
    height: 100vh;
    background-color: #F5F4E4;

    padding: 27px 30px 0;

    aspect-ratio: 3 / 2;

    &-wrapper {
        margin: auto;
        width: 290px;
        position: relative;
        transform-style: preserve-3d;
        transition: transform 1s;
    }

    &-wrapper__item {
        display: flex;
        flex-direction: column;

        gap: 20px;

        position: absolute;
        width: 100%;
        height: 100%;
        backface-visibility: visible;

        min-height: 565px;

        padding: 15px;

        box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.4);
        background-color: var(--white);
        background: linear-gradient(0.00deg, rgba(255, 255, 255, 0) 62.201%,rgba(255, 255, 255, 0.1) 98.026%),linear-gradient(0.00deg, rgba(0, 0, 0, 0),rgba(255, 255, 255, 0.3) 100%),rgb(255, 255, 255);

        // &::before,
        // &::after {
        //     content: '';
        //     position: absolute;
        //     width: 10%;
        //     height: 10%;
        //     background: white;
        //     transform-origin: center center;
        // }

        // &::before {
        //     transform: translateZ(-20px); /*    */
        // }

        // &::after {
        //     transform: translateX(20px) rotateY(90deg); /*    */
        // }
    }

    &-wrapper__item:nth-child(1) {
        transform: rotateY(0deg) translateZ(170px);
    }
    &-wrapper__item:nth-child(2) {
        transform: rotateY(90deg) translateZ(170px);
    }
    &-wrapper__item:nth-child(3) {
        transform: rotateY(180deg) translateZ(170px);
    }
    &-wrapper__item:nth-child(4) {
        transform: rotateY(270deg) translateZ(170px);
    }

    button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background: rgba(255, 255, 255, 0.7);
        border: none;
        padding: 10px;
        cursor: pointer;
        z-index: 1;
    }
    
    button.prev {
        left: 10px;
    }
    
    button.next {
        right: 10px;
    }

    &-wrapper__images {
        width: 100%;
        img {
            width: 100%;
        }
    }

    &-wrapper__info {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        gap: 10px;
    }
}