.cart {
    width: 100%;
    &-wrapper {
        display: flex;
        flex-direction: column;
        gap: 90px;

        .products-wrapper__img-wrap {
            max-width: 172px;
            height: 200px;
        }

        .products-wrapper__item {
            flex-direction: row;
            min-height: 200px;
        }

        .products-wrapper__info {
            position: relative;
        }
    }
}