.succes {
    padding-top: 110px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    gap: 50px;

    &-icon {
        display: flex;
        flex-shrink: 0;
        width: 111px;
        height: 111px;
    }

    span {
        font-size: 20px;
        font-weight: 400;
        line-height: 23px;

        width: 265px;
        text-align: center;
    }

    &-logo {
        display: flex;
        width: 215px;
        height: 21px;
    }
}