.form-group {
    display: flex;
    flex-direction: column;

    gap: 6px;

    label {
        font-size: 12px;
        font-weight: 400;
        line-height: 14px;
    }

    input {
        display: flex;
        align-items: flex-start;

        padding: 13px 19px;

        width: 100%;

        font-size: 18px;
        font-weight: 400;
        line-height: 21px;

        border: 1px solid var(--black);
        background-color: var(--white);
        border-radius: unset;
    }

    input::placeholder {
        letter-spacing: 2px;
    }
}

.form-row {
    display: flex;
    align-items: center;
    gap: 20px;
    width: 100%;
}