.personal-data {
    overflow: scroll;

    &.remove {
        display: none;
    }

    &__form {
        margin-top: 20px;
        display: flex;
        flex-direction: column;

        gap: 30px;
    }

    &__form-wrapper {
        display: flex;
        flex-direction: column;

        gap: 10px;
    }

    &__form-wrapper .form-group .select-input {
        display: flex;
        align-items: center;
        justify-content: space-between;

        padding: 13px 19px;

        border: 1px solid var(--black);
        background-color: var(--white);

        span {
            font-size: 18px;
            font-weight: 400;
            line-height: 21px;
            color: rgba(0, 0, 0, 0.5);
        }

        .arrow {
            transition: transform 0.3s;
        }

        .arrow.active {
            transform: rotate(180deg);
        }
    }

    &__form-wrapper .form-group .select-options {
        margin-top: -7px;
        display: none;
        flex-direction: column;

        width: 100%;

        background-color: var(--white);
        border: 1px solid var(--black);

        &.active {
            display: flex;
        }

        span {
            padding: 10px 16px;

            border-bottom: 1px solid var(--black);
        }

        span:last-child {
            border-bottom: unset;
        }
    }

    &__form-wrapper .date-picker {
        position: relative;
    }

    &__form-wrapper .form-group .calendar {
        display: none;
        position: absolute;
        padding: 10px;
        top: 100%;
        left: 0;
        width: 100%;
        max-width: 400px;
        border-radius: 4px;
        background: white;
        border: 1px solid #ccc;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        z-index: 1000;
        overflow: scroll;
    }
    
    &__form-wrapper .form-group .calendar-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
    }
    
    &__form-wrapper .form-group .calendar-weekdays, &__form-wrapper .form-group  .calendar-days {
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        gap: 5px;
    }
    
    &__form-wrapper .form-group .calendar-weekdays span {
        text-align: center;
        font-weight: bold;
    }
    
    &__form-wrapper .form-group .calendar-days span {
        text-align: center;
        padding: 10px;
        border-radius: 4px;
        cursor: pointer;
    }
    
    &__form-wrapper .form-group .calendar-days span:hover {
        background: #f0f0f0;
    }
    
    &__form-wrapper .form-group .calendar-days span.selected {
        background: #007bff;
        color: white;
    }
}