.payment {
    overflow: scroll;
    
    &-wrapper {
        margin-top: 20px;

        display: flex;
        flex-direction: column;
        gap: 44px;
    }

    &-wrapper__method,
    &-wrapper__method-others {
        display: flex;
        flex-direction: column;

        gap: 20px;

        h3 {
            font-size: 20px;
            font-weight: 700;
            line-height: 23px;
            text-transform: uppercase;
        }
    }

    &-wrapper__method-number {
        display: flex;
        align-items: center;
        gap: 10px;

        input[type="radio"] {
            display: none;
        }

        .radio-input {
            width: 20px;
            height: 20px;
            border: 2px solid #000;
            border-radius: 50%;
            display: inline-block;
            position: relative;
            margin-right: 10px;
            cursor: pointer;
        }

        .radio-input::after {
            content: '';
            width: 12px;
            height: 12px;
            background-color: #000;
            border-radius: 50%;
            position: absolute;
            top: 14%;
            left: 14%;
            transform: translate(-50, -50%);
            opacity: 0;
            transition: opacity 0.2s ease;
        }

        input[type="radio"]:checked ~ .radio-input::after {
            opacity: 1;
        }

        span {
            font-size: 20px;
            font-weight: 400;
            line-height: 23px;
        }
    }

    &-wrapper__method-others__list {
        display: flex;
        flex-direction: column;

        li {
            padding: 17px 0;

            border-bottom: 1px solid rgba(0, 0, 0, 0.2);;
        }

        li a {
            display: flex;
            align-items: center;
            gap: 18px;

            span {
                font-size: 20px;
                font-weight: 400;
                line-height: 23px;
                color: var(--black);
            }
        }

        li:last-child {
            border-bottom: unset;
        }
    }
}